import React, { useEffect, useState, useCallback } from "react";
import { useSound } from "use-sound";
import typewriterfx from "../sounds/typewritersound.mp3";

const useKeyboardKey = (map) => {
  //   const [keys, setKeys] = useReducer(keysReducer, initalKeyMapping);

  useEffect(() => {
    const keydownListener = (keydownEvent) => {
      const { keyCode } = keydownEvent;
      console.log(keydownEvent);
      const handler = map[keyCode];
      if (typeof handler === "function") {
        console.log(handler);
        handler();
      }
    };
    window.addEventListener("keydown", keydownListener);
    return () => window.removeEventListener("keydown", keydownListener, true);
  }, [map]);
};

function TypeWriter() {
  const [play] = useSound(typewriterfx, {
    sprite: {
      backspace: [0, 253.78684807256235],
      key1: [2000, 292.9478458049886],
      key2: [4000, 332.108843537415],
      key3: [6000, 228.208616780045],
      key4: [8000, 215.079365079365],
      key5: [10000, 295.759637188208],
      return: [12000, 992.925170068027],
      scrollDown: [15000, 245.850340136055],
      scrollUp: [17000, 329.274376417235],
      space: [19000, 223.605442176872],
    },
  });

  useKeyboardKey({
    8: () => play({ id: "backspace" }),
    13: () => play({ id: "return" }),
    32: () => play({ id: "space" }),
    33: () => play({ id: "ScrollUp" }),
    34: () => play({ id: "ScrollDown" }),
  });

  const playsound = () => {
    play({ id: "backspace", volume: 0.5 });
  };
  return (
    <>
      <button onClick={playsound}>Check the sound</button>
    </>
  );
  //   const mp3_backspace = document.getElementById("mp3-backspace");
}

export default TypeWriter;

